@charset "UTF-8";
* { outline: none; margin: 0; padding: 0; -webkit-box-sizing: border-box; box-sizing: border-box;
}
div, p, span, a, label, em, i, h1, h2, h3, h4, h5, h6 { margin: 0; padding: 0;
}
body { font: 12px "Arial", "Microsoft Yahei", "微软雅黑"; color: #666666; background: #f4f4f5; min-width: 1200px;
}
button, input, optgroup, select, textarea { font: 12px "Arial", "Microsoft Yahei", "微软雅黑"; color: #666666;
}
[class^="icon-"], [class*=" icon-"] { color: #999999; font-size: 18px;
}
ul li { list-style-type: none;
}
ol, ul { margin-top: 0; margin-bottom: 0;
}
span, i, em, label { display: inline-block; font-style: normal; font-weight: normal; margin: 0; padding: 0;
}
a { cursor: pointer; outline: none; display: inline-block; color: #666666; text-decoration: none;
}
a:hover { color: #2ca4ff; text-decoration: none;
}
a:active { color: #0083e4;
}
a:focus { color: #008df6; text-decoration: none;
}
