@charset "UTF-8";

/**** body *******/
.mainBox { position: relative;
}
.main-container { padding: 20px 20px 20px 20px; min-height: calc(100vh - 40px);
}
@media screen and (max-width: 1199px) {
.main-container { min-height: calc(100vh - 60px - 40px - 17px);
}
}
.content_shadow { -webkit-box-shadow: 0 0 4px rgba(197, 197, 197, 0.2); box-shadow: 0 0 4px rgba(197, 197, 197, 0.2); background: #ffffff; border-radius: 3px; padding: 20px;
}
.btn-group { position: relative; float: left; vertical-align: middle;
}
.text-indent { text-indent: 1em;
}

/** input **/
input[type="text"], input[type="password"] { width: 100%; border: 1px solid #dcdcdc; -webkit-box-shadow: none; box-shadow: none; border-radius: 3px; height: 30px; padding: 4px 10px; font-size: 12px; line-height: 20px; color: #666666; background-color: #ffffff;
}
input[type="text"]:hover, input[type="password"]:hover { border: 1px solid #dcdcdc; -webkit-box-shadow: none; box-shadow: none;
}
input[type="text"]:focus, input[type="password"]:focus { border: 1px solid #008df6; -webkit-box-shadow: none; box-shadow: none;
}
input[type="text"].error, input[type="password"].error { border: 1px solid #ed0000;
}
input::-ms-input-placeholder { color: #888888;
}
input::-moz-input-placeholder { color: #888888;
}
input::-webkit-input-placeholder { color: #888888;
}
input:-webkit-autofill { -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important; background-color: transparent !important; -webkit-text-fill-color: #888888 !important;
}
.el-input__prefix, .el-input__suffix { color: #999999;
}
.el-input__suffix { right: 0;
}
.el-input__icon { line-height: 30px; font-size: 16px;
}
.el-input { font-size: 12px;
}
.el-input__inner { background-color: #ffffff; border-radius: 3px; border: 1px solid #dcdcdc; color: #666666; font-size: inherit; height: 30px; line-height: 20px; padding: 4px 10px; width: 100%;
}
.el-input.is-disabled .el-input__inner { background-color: #eeeeee; border-color: #dcdcdc; color: #888888; cursor: not-allowed;
}
textarea { width: 100%; border: 1px solid #dcdcdc; -webkit-box-shadow: none; box-shadow: none; border-radius: 3px; height: 30px; padding: 4px 10px; font-size: 12px; line-height: 20px; color: #666666; background-color: #ffffff;
}
textarea:focus { border: 1px solid #008df6; -webkit-box-shadow: none; box-shadow: none;
