}
.el-select .el-input__inner { overflow: hidden; white-space: nowrap; text-overflow: ellipsis;
}
.el-select .el-input__inner::-webkit-input-placeholder { color: #4e4e4e;
}
.el-select .el-input__inner::-moz-placeholder { color: #4e4e4e;
}
.el-select .el-input__inner:-moz-placeholder { color: #4e4e4e;
}
.el-select .el-input__inner:-ms-input-placeholder { color: #4e4e4e;
}
@charset "UTF-8";
.ZKInfo .msgFolder { position: relative;
}
.ZKInfo .msgFolder .msgFolderCont { display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column; cursor: pointer;
}
.ZKInfo .msgFolder .msgFolderCont:hover { background: #e5f8ff;
}
.ZKInfo .msgFolder .msgFolder-img { display: inline-block; width: 54px; height: 47px; position: relative; background: url(assets/fonts-svg/newSvg/fileb-big.svg) no-repeat;
}
.ZKInfo .msgFolder .msgFolderName { margin-top: 7px; color: #4e4e4e;
}
.floatMsg { width: 380px; height: 230px; border-radius: 2px; box-shadow: 0 0px 3px rgba(0, 0, 0, 0.175); -webkit-box-shadow: 0 0px 3px rgba(0, 0, 0, 0.175); position: absolute; background: #ffffff; left: 40px; top: 50px;
}
.floatMsg .blue-top { height: 8px; background: #1473e6;
}
.floatMsg .content { width: 100%; height: 100%; padding: 10px 20px; padding-bottom: 0;
}
.floatMsg .content h4 { color: #000000; margin-bottom: 10px;
}
.floatMsg .content h4::before { content: ''; display: inline-block; width: 7px; height: 7px; background-image: -webkit-gradient(linear, left top, right top, from(#2cb6fe), to(#0678ff)); background-image: linear-gradient(to right, #2cb6fe, #0678ff); border-radius: 50%; margin-right: 7px; margin-bottom: 1px;
}
.floatMsg .content .nodeMsg { width: 100%; height: 186px; border: 1px solid #dedede; border-bottom: none; padding: 10px 13px 0 13px; overflow: auto;
}
.floatMsg .content .nodeMsg::-webkit-scrollbar { width: 10px; /*对垂直流动条有效*/
}
.floatMsg .content .nodeMsg::-webkit-scrollbar-thumb { border-radius: 2px; background-color: #959595;
}
.floatMsg .content .nodeMsg .conMsg { color: #888888;
}
.el-popover { position: absolute; background: #fff; min-width: 420px; border: none; padding: 0; z-index: 2000; color: #888888; line-height: 1.4; text-align: justify; font-size: 12px; -webkit-box-shadow: none; box-shadow: none; word-break: break-all;
}
.el-popper { margin-top: 30px;
}
.el-popper[x-placement^=left] { margin-right: -40px;
}
.el-popper[x-placement^=right] { margin-left: -110px;
}



